



































































import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';
import Echarts from '@/components/Statement/echarts.vue';

@Component({
    components: { Echarts }
})
export default class TagRecordList extends Vue {
    @Prop() data!: {
        tagRecordList: string | any | number | string;
    };
    @Prop() option!: {};
    @Prop() top!: [];

    get totalOptions() {
        return {
            dataset: {
                source: [
                    ['sum', 'type'],
                    [this.data.tagRecordList[2], '收入'],
                    [this.data.tagRecordList[3], '支出'],
                    [(this.data.tagRecordList[2] - this.data.tagRecordList[3]), '余额'],

                ]
            },
            grid: {
                top: '15%',
                bottom: '10%',
                left: '22%',
                right: '3%',
                width: '66%',
            },
            xAxis: {
                name: '元',
                show: false,
                splitLine: { show: false },
                axisLabel: {
                    color: '#999'
                },
                axisTick: { //x轴刻度线
                    show: false
                },
                axisLine: { //x轴
                    show: false
                }
            },
            yAxis: {
                type: 'category',
                //maxInterval: 300,
                splitLine: { show: false },
                fontSize: '35',
                axisLabel: {
                    margin: 12,
                    textStyle: {
                        fontSize : 12      //更改坐标轴文字大小
                    },
                    color: function (params: any, index: any) {
                        const colorList = ['#9fd26a', '#ea5e69', '#a4789f'];
                        return colorList[index];
                    },
                },
                axisTick: { //y轴刻度线
                    show: false
                },
                axisLine: { //y轴
                    show: false
                }
            },
            series: [
                {
                    type: 'bar',
                    barWidth: 7,
                    encode: {
                        // Map the "sum" column to X axis.
                        x: 'sum',
                        // Map the "type" column to Y axis
                        y: 'type'
                    },

                    itemStyle: {
                        normal: {

                            label: {
                                formatter: '￥' + '{@sum}' + '元',
                                show: true,
                                position: 'right',
                                textStyle: {
                                    fontWeight: 'bolder',
                                    fontSize: '8',
                                    color: '#fff'
                                },
                            },
                            /*
                            随机显示
                            color:function(d){
                                return "#"+Math.floor(Math.random()*(256*256*256-1)).toString(16);
                            }*/

                            /*定制显示（按顺序）*/
                            color: function (params: any) {
                                const colorList = ['#9fd26a', '#ea5e69', '#a4789f'];
                                return colorList[params.dataIndex];
                            }
                        },

                    },
                }
            ]
        };
    }

    get topOptions() {
        return {};
    }

    isPay(output: any) {
        return output.selectedTag === '支出';
    }

    isEarning(output: any) {
        return output.selectedTag === '收入';
    }

}

